<template>
  <div class="home">
    <h1>{{ greet() }}</h1>
    <h2>Willkommen auf meinem Portfolio!</h2>
    <p class="subtitle">
      Mein Name ist Marc. Ich bin Informatiker und schliesse im Sommer 2022
      meine Lehre bei Swisscom ab. Auf dieser Webseite findest du die
      wichtigsten Informationen über mich.
    </p>
  </div>
</template>

<script>
export default {
  name: "Home",
  components: {},
  data() {
    return {};
  },
  methods: {
    greet() {
      const hour = new Date().getHours();
      let greeting = "Hallo!";

      if (hour < 12) greeting = "Guten Morgen!";
      else if (hour < 18) greeting = "Hallo!";
      else greeting = "Guten Abend!";

      return greeting;
    },
  },
};
</script>

