<template>
  <div id="app">
    <div class="shape-blob"></div>
    <div class="shape-blob one"></div>
    <div class="shape-blob two"></div>

    <nav>
      <router-link to="/">Home</router-link>
      <router-link to="/about">Über mich</router-link>
      <router-link to="/projects">Projekte</router-link>
      <router-link to="/skills">Kenntnisse</router-link>
      <router-link to="/contact">Kontakt</router-link>
    </nav>

    <div id="nav-mobile">
      <button class="previous" @click="previous()">
        <b-icon icon="arrow-left" size="is-medium"> </b-icon>
      </button>
      <div class="path">{{ currentPage }}</div>
      <button class="next" @click="next()">
        <b-icon icon="arrow-right" size="is-medium"> </b-icon>
      </button>
    </div>

    <transition name="fade" mode="out-in">
      <router-view class="view" />
    </transition>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      currentPage: "",
      routes: ["/", "/about", "/projects", "/skills", "/contact"],
      routeNames: ["Home", "Über mich", "Projekte", "Kenntnisse", "Kontakt"],
    };
  },
  mounted() {
    this.updatePath();
  },
  updated() {
    this.updatePath();
  },
  methods: {
    next() {
      if (this.$route.path == this.routes[this.routes.length - 1])
        this.$router.push(this.routes[0]);
      else
        this.$router.push(
          this.routes[this.routes.indexOf(this.$route.path) + 1]
        );
    },
    previous() {
      if (this.$route.path == this.routes[0])
        this.$router.push(this.routes[this.routes.length - 1]);
      else
        this.$router.push(
          this.routes[this.routes.indexOf(this.$route.path) - 1]
        );
    },

    updatePath() {
      this.currentPage = this.routeNames[this.routes.indexOf(this.$route.path)];
    },
  },
};
</script>

